<template>
  <div id="tablet">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: 100%" id="ledgerQuery"></QueryBox>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row :max-height="tableHeight"></Table>
        <div class="page_box">
          <Page :total="total" :page-size="pageSize" :current.sync="pageNum" @on-change="pageChange" show-total/>
        </div>
      </div>
    </div>
    <!-- 详情列表 -->
    <div class="extract_box" :class="{detailActive:detailActive}">
      <div class="queryBox" style="text-align: right">
        <QueryBox :formArr="queryFrom2" @query="getQueryData2" style="width: calc(100% - 80px)"></QueryBox>
        <Button size="small" type="primary" icon="ios-arrow-back" @click="detailActive=false">返回</Button>
      </div>
      <div style="padding: 0 15px 15px;height: calc(100% - 70px)">
        <Row style="height: 100%">
          <Col span="24">
            <Table border stripe :data="data2" :columns="columns2" :max-height="tableHeight"></Table>
            <Page :total="total1"  show-total :page-size="pageSize1" :current.sync="pageNum1" style="width: 100%;padding: 15px 0;text-align: right"/>
          </Col>
        </Row>
      </div>
    </div>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import { taizhanglist } from '@/api/systemManage'
import {
  getAccountList,
  detailWithStage
} from '@/api/remoteSensing'
import { renderTime, cacuDMS } from '@/utils/common'
export default {
  name: 'extracTable',
  data () {
    return {
      /* 查询框查询条件 */
      queryData: {},
      /* 表格高度 */
      tableHeight: Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40,
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '人类活动监管台账名称',
          align: 'center',
          minWidth: 200,
          key: 'name'
        },
        {
          title: '组别',
          key: 'type',
          align: 'center',
          minWidth: 150
        },
        {
          title: '日期',
          align: 'center',
          minWidth: 200,
          render: (h, params) => {
            return h('span', renderTime(params.row.updateDate === null ? params.row.createDate : params.row.updateDate))
          }
        },
        {
          title: '人类活动监管台账说明',
          align: 'center',
          minWidth: 200,
          key: 'remark'
        },
        {
          title: '人类活动版块数量',
          align: 'center',
          minWidth: 200,
          key: 'sonCount'
        },
        {
          title: '人类活动面积(亩)',
          align: 'center',
          minWidth: 200,
          key: 'sonArea',
          render: (h, params) => {
            return h('span', (params.row.sonArea * 1).toFixed(2))
          }
        },
        {
          title: '操作',
          key: 'address',
          width: 120,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-eye'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.tzId = params.row.id
                    this.showAccountDetail(params.row.id)
                  }
                }
              }, '查看详情')
            ])
          }
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      loading: false,
      /* 台账详情面板 */
      detailActive: false,
      /* 人类活动详情列表数据 */
      data2: [],
      /* 人类活动详情列表的列设置 */
      columns2: [
        {
          title: '序号',
          type: 'index',
          align: 'center',
          width: 60
        },
        {
          title: '斑块编号',
          align: 'center',
          minWidth: 200,
          key: 'cd001'
        },
        {
          title: '斑块名称',
          align: 'center',
          minWidth: 200,
          key: 'activeName'
        },
        {
          title: '问题描述',
          align: 'center',
          minWidth: 200,
          key: 'ck015',
          render: (h, params) => {
            if (params.row.st4ScsCk.ck015) {
              return h('span', params.row.st4ScsCk.ck015)
            } else {
              return h('span', '')
            }
          }
        },
        {
          title: '活动设施现状',
          align: 'center',
          minWidth: 200,
          key: 'ck012',
          render: (h, params) => {
            if (params.row.st4ScsCk.ck012) {
              return h('span', params.row.st4ScsCk.ck012)
            } else {
              return h('span', '')
            }
          }
        },
        {
          title: '占地面积(亩)',
          align: 'center',
          minWidth: 150,
          key: 'ck011',
          render: (h, params) => {
            if (params.row.st4ScsCk.ck011) {
              return h('span', (params.row.st4ScsCk.ck011 * 1).toFixed(2))
            } else {
              return h('span', '')
            }
          }
        },
        {
          title: '实际功能区',
          align: 'center',
          minWidth: 150,
          key: 'ck006',
          render: (h, params) => {
            if (params.row.st4ScsCk.ck006) {
              return h('span', params.row.st4ScsCk.ck006)
            } else {
              return h('span', '')
            }
          }
        },
        {
          title: '建设时间',
          align: 'center',
          minWidth: 150,
          key: 'ck020',
          render: (h, params) => {
            if (params.row.st4ScsCk.ck020) {
              return h('span', params.row.st4ScsCk.ck020)
            } else {
              return h('span', '')
            }
          }
        },
        {
          title: '变化类型',
          align: 'center',
          minWidth: 200,
          key: 'ck013',
          render: (h, params) => {
            if (params.row.st4ScsCk.ck013) {
              return h('span', params.row.st4ScsCk.ck013)
            } else {
              return h('span', '')
            }
          }
        },
        {
          title: '活动设施类型',
          align: 'center',
          minWidth: 200,
          key: 'ck016',
          render: (h, params) => {
            if (params.row.st4ScsCk.ck016) {
              return h('span', params.row.st4ScsCk.ck016)
            } else {
              return h('span', '')
            }
          }
        }
      ],
      /* 人类活动详情列表的分页器参数 */
      pageSize1: 20,
      pageNum1: 10,
      total1: 0,
      tzId: '', // 台账id
      tzzu: [], // 台账组信息
      /* 查询框查询条件 */
      queryData2: {}
    }
  },
  computed: {
    // 查询表单
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'name',
          title: '人类活动监管台账名称'
        }
      ]
    },
    // 详情查询表单
    queryFrom2: function () {
      return [
        {
          type: 1,
          field: 'number',
          title: '斑块编号'
        },
        {
          type: 1,
          field: 'name',
          title: '斑块名称'
        }
      ]
    }
  },
  components: {
    QueryBox
  },
  created () {
    this.getTableList()
    this.getTaizhagnzubie()
  },
  mounted () {
    // 窗口改变时图表自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableList()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取台账组别的接口 */
    getTaizhagnzubie () {
      taizhanglist({
        data: {
          pageIndex: 1,
          pageSize: 20,
          name: ''
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.tzzu = res.data.data.poList
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      })
    },
    /* 获取字段列表 */
    getTableList () {
      this.loading = true
      getAccountList({
        'data': {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          ...this.queryData
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    },
    /* 获取查询数据 */
    getQueryData2 (data) {
      this.queryData2 = data
      this.pageNum1 = 1
      this.showAccountDetail(this.tzId)
    },
    /* 展开显示人类活动台账的详情 */
    showAccountDetail (id) {
      this.detailActive = true
      this.pageNum1 = 1
      detailWithStage({
        data: {
          id: id,
          pageSize: this.pageSize1,
          pageNum: this.pageNum1,
          ...this.queryData2
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.data2 = res.data.data.rows[0].st4ScsCdList
          this.total1 = res.data.data.total
          console.log(this.data2)
        } else {
          this.$Notice.error({ title: '获取人类活动台账详情失败', desc: res.data.msg })
        }
      })
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .btn-exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
    .btn-download {
      background-color: #1ccf20;
      border-color: #1ccf20;
      &:hover, &:focus {
        background-color: #13bb17;
        border-color: #13bb17;
      }
    }
    .btn-tabBtn {
      background: rgb(16,186,204);
      border-color: rgb(16,186,204);
      margin-right: 10px;
      &:hover {
        background: #13B1CC;
        border-color: #13B1CC;
      }
    }
    .iconfont {
      margin-right: 4px;
      &.icon-xuanzewenjian {
        font-size: 12px;
      }
      &.icon-xiazai {
        font-size: 13px;
      }
      &.icon-upload {
        font-size: 12px;
      }
      &.icon-leimupinleifenleileibie {
        font-size: 12px;
      }
    }
    .queryBox {
      height: 50px;
      line-height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .extract_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
    .detailActive{
      right: 0;
    }
    .btn-import {
      background-color: rgb(194,146,1);
      border-color: rgb(194,146,1);
      &:hover, &:focus {
        background-color: #B28301;
        border-color: #B28301;
      }
    }
  }
</style>
